}
header{
    background: #fafafa;
    margin:0px;
    padding:10px 0px 20px 0px;

    /* width:95%;
    max-width:950px; */
    margin:0 auto;
    margin-top:10px;
}
header h1{
    font-family: 'Didact Gothic';

    font-size: 250%;
