  background: #fafafa;
  margin : 0px 0px 10px 0px;
  padding : 15px 2%;
}
.buttonBase {
  background: #f16272;
  border: none;
  color: #fff;
  cursor: pointer;
  /* height: 50px; */
  /* font-family: 'Open Sans', 'Rounded Mplus 1c', sans-serif; */
  font-size: 1em;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: background 0.3s ease-in-out;
  transition: background 0.3s ease-in-out;
  /* width: 100%; */
